import { Button, Typography } from '@mui/material';
import { useCallback, useEffect, useState } from 'react';
import { createWorker } from 'tesseract.js';
import Cropper from 'react-easy-crop';
import getCroppedImg from './helper';

const MTGDB = () => {
  const [img, setImg] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [crop, setCrop] = useState({ x: 0, y: 0 });
  const [zoom, setZoom] = useState(1);
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null);
  const [croppedImage, setCroppedImage] = useState(null) as any;
  const [rotation, setRotation] = useState(0);
  const [text, setText] = useState('');

  const handleClick = async () => {
    let worker = createWorker({
      logger: (m) => console.log(m),
    });
    await worker.load();
    await worker.loadLanguage('eng');
    await worker.initialize('eng');
    const {
      data: { text },
    } = await worker.recognize(croppedImage);
    // console.log(text);
    setText(text);
    await worker.terminate();
  };

  const handleChange = (event: any) => {
    setImg(URL.createObjectURL(event.target.files[0]));
    setIsLoading(false);
  };

  const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels);
  }, []);

  const showCroppedImage = useCallback(async () => {
    try {
      const croppedImage = await getCroppedImg(img, croppedAreaPixels, rotation);
      console.log('donee', { croppedImage });
      setCroppedImage(croppedImage);
    } catch (e) {
      console.error(e);
    }
  }, [croppedAreaPixels, rotation, img]);

  return (
    <div>
      <input type="file" accept="image/*" capture="environment" onChange={handleChange} />

      <Button onClick={handleClick}>do magic</Button>

      {text}

      <div style={{ position: 'relative', height: '480px', width: '480px' }}>
        {!isLoading && (
          <Cropper
            image={img}
            crop={crop}
            zoom={zoom}
            aspect={2 / 0.5}
            onCropChange={setCrop}
            onCropComplete={onCropComplete}
            onZoomChange={setZoom}
          />
        )}
      </div>
      <Button onClick={showCroppedImage} variant="contained" color="primary">
        Crop
      </Button>
      <img src={croppedImage} alt="cropped" style={{ maxWidth: '350px' }} />
    </div>
  );
};

export default MTGDB;
